body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.dashboard-page{
    padding: 40px 40px 40px 40px;
    background: linear-gradient(180deg, #FC8A8A 0%, #FFD3B3 50%);
    background-size: 100vw 189px;
    background-repeat: no-repeat;
}
.chart-holder{
    height: calc(100vw - 84px);
    margin-top: 15px;
    width: calc(100vw - 84px);
    border: 2px solid #FFDCDC;
}
.stats-holder{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 4fr 3fr;
    height: 300px;
    grid-row-gap: 20px;
    row-gap: 20px;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    margin-top: 20px;
}
.month-total-money{
    text-align: center;
    background-color: #FFAFA4 !important;
    grid-column: 1/3;
    grid-row: 1/2;
}
.month-total-money > p {
    color: white;
    font-size: 10px;
    padding: 15px 20px 10px 20px;
}
.month-drink-limit{
    background-color: #FFDCDC !important;
    text-align: center;
    padding: 80px 5px 0 5px;
    grid-column: 3/4;
    grid-row: 1/3;
}
.month-drink-limit > p{
    color: #FC8A8A;
    font-size: 12px;
}
.month-total-drinks{
    padding: 20px;
    text-align: center;
    grid-column: 1/3;
    grid-row: 2/3;
}
.daily-chart{
    margin-top: 20px;
}
.month-total-drinks > p {
    color: #FC8A8A;
}
.month-total-drinks > h2 {
    color: #FC8A8A;
}

.add-modal{
    position: fixed;
    top: calc( 50vh - 230px);
    height: 390px;
    width: calc( 100vw - 84px);
    left: 38px;
    background-color: white;
    border: 2px solid #FFDCDC;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 27px;
}
.add-input{
    width: 100%;
}
.add-button-holder{
    text-align: center;
    width: 100%;
}
.user-modal{
    position: fixed;
    top: calc( 50vh - 140px);
    height: 300px;
    width: calc( 100vw - 50px);
    left: 25px;
    background-color: white;
    border: 3px solid #FFDCDC;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 60px 40px 40px 40px;
}
.user-input{
    width: 100%;
}
.update-button-holder{
    text-align: center;
    width: 100%;
}
.user-avatar{
    border-radius: 50%;
    height: 100px;
    width: 100px;
    position: absolute;
    top: -60px;
    left: calc( 50vw - 82px);
    margin: 0;
    border: 4px solid #FFDCDC;
}
.history-page{
    padding: 40px 40px 40px 40px;
}
.history-drink-label{
    display: grid;
    grid-template-columns: 1fr 1fr 40px;
    grid-template-rows: 25px 35px;
}
p{
    margin: 0 !important;
    font-family: Poppins;
}
.drink-place{
    color: #F68080;
}
.drink-time{
    text-align: right;
    color: grey;
    font-size: 12px;
    padding-top: 8px;
}
.drink-price{
    font-size: 24px;
    text-align: right;
}
.drink-description{
    text-align: center;
    padding-top: 15px;
    padding-bottom: 15px;
}
img{
    width: 60vw;
    background-color: #FFC9C2;
    margin: 30px calc(50% - 30vw) 0 calc(50% - 30vw);
}
.thaman-color{
    border-bottom: 2px solid #F68080;
    margin-bottom: 10px;
}
h3 span{
    color: black;
}
.history-total{
    margin-top: 25px !important;
    text-align: right;
}
.expand-icon{
    margin-top: 10px;
    color: #F68080;
}
.drink-options{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 60px;
    margin-bottom: 5px;
}
.login-page{
    height: 100vh;
    width: 100vw;
    text-align: center;
    background: linear-gradient(180deg, #FC8A8A 0%, #FFD3B3 100%);
}
.login-logo{
    height: 100vw;
    max-height: 50vh;
    width: 100vw;
    max-width: 50vh;
    margin: 0 auto;
    padding-top: 7vh;
    background-image: url(/static/media/logo.609a27ca.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}
.bottom-nav{
    width: 100%;
    position: fixed;
    bottom: 0;
}
.center-fab{
    height: 100px;
    width: 100px;
    box-sizing: border-box;
    padding-top: 17px;
    margin-top: -35px;
    text-align: center;
    border: 5px solid #FFAFA4;
    border-radius: 50%;
    color: #FFAFA4;
    background-color: white;
    z-index: 1000;
}
.page{
    height: calc(100vh - 56px);
    box-sizing: border-box;
    width: 100vw;
    overflow-y: scroll;
    overflow-x: hidden;
    padding-bottom: 40px;
    position: relative;
}
.avatar-button{
    height: 37px;
    width: 37px;
    border-radius: 50%;
    position: absolute;
    right: -36px;
    top: 5px;
}
@-moz-document url-prefix() {
    .center-fab{
        margin-top: -5px;
    }
}
@media only screen and (max-width: 350px) {
    h2 {
        font-size: 30px !important;
    }
    h3 {
        font-size: 20px !important;
    }
    h4 {
      font-size: 20px !important;
    }
  }
